@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch&display=swap');

*{
  margin: 0;
  padding: 0;
 box-sizing: box-border;
 font-family: 'Chakra Petch', sans-serif;
 font-weight: bold;
 -webkit-tap-highlight-color: transparent;

}
.App{
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}

.container-cal{
  margin: auto;
  padding: 5px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  /* height: 700px; */
}

.text-area{
  padding: 5px;
  /* background-color: blueviolet; */
  height: auto;
}
.text-box-area{
  padding: 2px;
  height: auto;
  /* overflow-y: auto; */
}

.text-area > p{
  /* font-size: 1.5rem; */
  font-size: 4rem;
  text-align:right;
margin : 1.5rem .4rem;
}

input[type='text'] {
  width: 100%;
  height: 40px;
  font-size: 1.5rem;
  text-align: right;
  margin: 2rem 0;
  /* padding-right: 10px; */
}

.back-btn{
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  border-bottom: solid 1px rgb(160, 156, 156);
  /* border-top: solid 1px red; */
  margin: .4rem;
  user-select: none;
}

.buttons-all{
  
  /* background-color: blanchedalmond; */
  padding: 4px;
  height: auto;
}

button {
  width: 75px;
  height: 75px;
  font-size: 25px;
  border-radius: 50%;
  margin: 15px 10px;
border : none;
  outline:none;
  cursor: pointer;
}

.equal{
  background-color: #12af27;
}

.clear1{
  color: #c50000;
}

.operator{
  color: #068016;
  font-weight: bold;
}

@media screen and (max-width: 600px) {
  .container-cal{
    width: 100%;
    height: 100vh;
  }
  button {
    width: 75px;
    height: 75px;
    font-size: 30px;
    margin: 15px 10px;
  
  
  }
}

@media screen and (max-width: 400px) {
  
  button {
    width: 60px;
    height: 60px;
    font-size: 20px;
    margin: 15px 5px;
  
  
  }
}